* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Times New Roman, Times, serif;
}

body {
  background-image: linear-gradient(to right, #605e7e, #6b73c1, #37b3cc);
  margin: 50px 2%;
}

.container-title {
  text-align: center;
  color: #fff;
  text-shadow: 3px 1px #000;
  margin: 0 0 30px;
  font-size: 25px;
}

.inputcol {
  grid-template-columns: 60% 10%;
  justify-content: center;
  column-gap: 5px;
  margin-top: 10px;
  display: grid;
}

.textarea {
  min-height: 50px;
  height: 50px;
  max-width: 100%;
  min-width: 100%;
  border-color: #333;
  border-radius: 10px;
  padding: 10px;
  font-size: 20px;
  overflow-x: hidden;
  overflow-y: auto;
}

.textarea::-webkit-scrollbar-track {
  background-color: #333;
  border-radius: 10px;
}

.textarea::-webkit-scrollbar {
  width: 10px;
  cursor: pointer;
}

.textarea::-webkit-scrollbar-thumb {
  cursor: pointer;
  background-color: #8f8acc;
  border-radius: 10px;
}

.textarea:focus {
  outline: none;
}

.buttoninput {
  background-color: #fff;
  border-color: #333;
  border-radius: 10px;
  font-size: 20px;
}

.buttoninput:hover {
  color: #fff;
  background-color: #8f8acc;
}

.todolist {
  margin-top: 20px;
}

.itemall {
  grid-template-columns: 60% 5% 5%;
  justify-content: center;
  margin-bottom: 2px;
  display: grid;
}

.item, .check-button, .trash-button {
  min-height: 50px;
  background-color: #fff;
  border: none;
  padding: 10px;
}

.item {
  word-wrap: break-word;
  word-break: break-all;
  border-radius: 10px 0 0 10px;
  align-content: center;
  font-size: 20px;
  display: grid;
}

.check-button, .trash-button {
  font-size: 15px;
}

.trash-button {
  border-radius: 0 10px 10px 0;
}

.check-button:hover {
  color: #fff;
  background-color: #37b3cc;
}

.trash-button:hover {
  color: #fff;
  background-color: #6b73c1;
}

.checklist {
  text-decoration: line-through;
}

.fa-check, .fa-trash {
  pointer-events: none;
}

button, .check-button, .trash-button {
  cursor: pointer;
}

